<template>
	<view class="content">
		<narBar :type="narBar"  type2Name="收藏"  @gotoNarBar="gotoNarBar()" @serach="serach"></narBar>
		<view class="warp">
		<view class="main-content">
			<view v-for="(item,index) in topicList" @click="gotoDiscuss(item.id)" class="topic-item" :key="index">
				<myTopic :topicItem="item"></myTopic>
			</view>
		</view>
		
		<myLoad :status="status"></myLoad>
		<!-- <view @click="gotoAddCard" class="add">
			<span></span>
		</view> -->
		</view>
		
		<uni-fab ref="fab"  :pattern="pattern" :content="content" :horizontal="horizontal" :vertical="vertical"
				:direction="direction" @trigger="trigger" @fabClick="fabClick" />
		
	</view>
</template>


<script>
	import bottomInput from '@/components/bottomInput/bottomInput.vue'
	import narBar from "@/components/narBar/narBar.vue"
	import myTopic from "@/components/myTopic/myTopic.vue"
	import myCard from "@/components/myCard/myCard.vue"
	import {getTopicList} from "@/api/index.js"
	import {mapGetters} from "vuex"
	export default {
		data() {
			return {
				status: 'loading',
				narBar:1,
				topicList:[],
				// 查询参数
				queryParams: {
					pageNum: 1,
					pageSize: 3,
					orderType: 1,
					name:""
				},
				//总条数
				total:0,
				
				//悬浮窗：
					pattern: {
						color: '#7A7E83',
						backgroundColor: '#fff',
						selectedColor: '#fac843',
						buttonColor: '#fac843',
						iconColor: '#fff'
					},
					
					horizontal: 'right',
					vertical: 'bottom',
					direction: 'vertical',
					content: [{
						iconPath: 'iconfont icon-fabiao icon-active',
						// selectedIconPath: '/static/image-active.png',
						text: '发布',
						active: false
					},
					{
						iconPath: 'iconfont icon-icon- icon-active',
						// selectedIconPath: '/static/home-active.png',
						text: '顶部',
						active: false
					},
					{
						iconPath: 'iconfont icon-shuaxin icon-active',
						// selectedIconPath: '/static/star-active.png',
						text: '刷新',
						active: false
					}
				]
			}
		},
		components:{
			narBar,
			myTopic,
			myCard,
			bottomInput
			
		},
		computed:{
			...mapGetters('user',['getUserId'])
		},
		
		onLoad() {
			this.getTopicList();
		},
		//下拉刷新
		onPullDownRefresh() {
			this.queryParams.name = '';
			this.queryParams.pageNum = 1;
			this.topicList = [];
			this.getTopicList().then(()=>{uni.stopPullDownRefresh()})
		},
		
		methods: {
			
			serach(val){
				this.queryParams.pageNum=1
				this.queryParams.name=val;
				this.topicList=[];
				this.getTopicList();
			},
			 pullTop() {
					uni.pageScrollTo({
						scrollTop: 0,//滚动到距离顶部为0
						duration: 500//滚动时长
					})
				},
			gotoAddTopic(){
				uni.navigateTo({
					url:"/pages/addTopic/addTopic"
				})
			},
			//分页
			handleCurrentChange(){
				if(!this.isMore()){
					this.status = "nomore"
					return ;
				}
				// Vue.set(this.queryParams,pageNum,page.pageNum)
				this.queryParams.pageNum++;
				this.queryParams.pageSize;
				this.getTopicList();
			},
			/**
			 * 拿话题列表
			 */
			async getTopicList(){
				
				this.status = "loading"
				const res = await getTopicList(this.queryParams).catch(err=>{
					
				})
				
				this.topicList = [...this.topicList,...res.data.rows];
				this.total =  res.data.total
				
				if(!this.isMore())
					this.status = "nomore"
				else
					this.status = "more"
					
			},
			isMore(){
				if(this.queryParams.pageNum*this.queryParams.pageSize >= this.total){
					console.log("无数据")
					return false;
				}
				return true;
			},
			//发送信息
			sead(val){
				//获取到val,调用子组件方法发送信息
			},
			gotoDiscuss(id){
				uni.navigateTo({
					url:`/pages/discuss/discuss?topicId=${id}`
				})
			},
			gotoNarBar(val){
				console.log(val)
				this.narBar = val;
			},
			
			isMore(){
				if(this.queryParams.pageNum*this.queryParams.pageSize >= this.total){
					console.log("无数据")
					return false;
				}
				return true;
			},
			/**
			 * 悬浮按钮项点击事件
			 * @param {Object} e
			 */
			trigger(e) {
				console.log(e)
				this.$refs.fab.close();
				if(e.index==0){
					this.gotoAddTopic();
				} else if(e.index==1){
					this.pullTop();
				} else{
					// this.pullTop();
					uni.startPullDownRefresh();
				}
				// this.content[e.index].active = !e.item.active
			
				
			},
			/**
			 * 悬浮按钮点击事件
			 */
			fabClick() {
				// uni.showToast({
				// 	title: '点击了悬浮按钮',
				// 	icon: 'none'
				// })
			}
		},
		watch:{
			narBar(newVal,owlVal){
				this.queryParams.pageNum=1
				this.queryParams.name=""
				this.queryParams.orderType = newVal
				this.topicList=[];
				this.getTopicList();
			}
		},
		//触底
		onReachBottom(){
			console.log("触底了")
			this.handleCurrentChange();
		}
	}
</script>

<style>

.topic-item{
	margin-bottom: 50px;
}
.topic-item:last-child{
	margin-bottom: 0 !important;
}


.title{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;

	margin: 20px 0;
}

</style>
